// This content script's sole purpose is to pass messages from Lustre pages to the background script in a cross browser way
(() => {
  window.addEventListener('message', (event) => {
    /**
     * @param {string} messageId - The message unique id
     * @param {string} direction - One of [`toContent`, `fromContent`]. Used to ignore the response messages sent from this script to the page
     * @param {object} message - The message's data content. Passed to the background script as is
     */
    const { messageId, direction, message } = event.data
    if (!messageId || direction !== 'toContent') {
      return
    }
    if (event.origin === window.origin) {
      chrome.runtime.sendMessage(message, (response) => {
        const data = { messageId, response, direction: 'fromContent' }
        window.postMessage(data)
      })
    }
  }, false)

  // Add attr to HTML element to signal that the installed version supports this communication method
  document.documentElement.dataset.luxCommunication = true
})()
